// State
const State = props => {
  // props: stateId
  const { id } = props.match.params;
  return (
    <div className="pageContainer">
      <StateContainer stateId={id} />
    </div>
  );
};

// State > StateContainer
class StateContainer extends React.Component {
  constructor(props) {
    super(props); // props: stateId
    this.state = {
      activeTab: "usHouse",
      pressed: []
    };
    this.toggle = this.toggle.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  componentWillMount() {
    // set activeTab to whatever the first legislature is, in case usHouse is not up for election in this state
    this.setState({
      activeTab: stateInfo[this.props.stateId].legislatures[0].id
    });
  }

  indexGet(pressed, arr) {
    for (var i = 0; i < pressed.length; i++) {
      let index = pressed[i].indexOf(arr[3]);
      if (index > -1) {
        return +i;
      }
    }
  }

  handleChange(type, selected, remove) {
    const arr = selected.split(",");
    const index = this.indexGet(this.state.pressed, arr);
    const pressed = this.state.pressed;

    switch (type) {
      case "add":
        pressed.push(arr);
        break;
      case "delete":
        pressed.splice(index, 1);
        break;
      case "change":
        const removeArr = remove.split(",");
        const removeIndex = this.indexGet(this.state.pressed, removeArr);
        pressed.splice(removeIndex, 1, arr);
        break;
      default:
        console.log("switch defaulted");
        break;
    }
    this.setState({ pressed: pressed });
  }

  toggle(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab
      });
    }
  }

  render() {
    const stateName = stateInfo[this.props.stateId].name;
    const legislatures = stateInfo[this.props.stateId].legislatures;

    // Nav Tabs
    const navTabs = stateInfo[this.props.stateId]["legislatures"].map(x => (
      // x = stateInfo.{stateId}.legislatures[i]
      <NavItem key={x.id}>
        <NavLink
          className={classnames({
            active: this.state.activeTab === x.id
          })}
          onClick={() => {
            this.toggle(x.id);
          }}
        >
          {x.name}
        </NavLink>
      </NavItem>
    ));

    // Tab Panes
    const tabPanes = legislatures.map(x => (
      // x = stateInfo.{stateId}.legislatures[i]
      <TabPane key={x.id + "tabpane"} tabId={x.id}>
        {x.districts.map(y => (
          // x = stateInfo.{stateId}.legislatures[i]districts[i]
          <div key={y.name + "district"}>
            <p className="districtNameInTabs">
              {x.name} – {y.name}
            </p>
            <div className="">
              <ButtonGroup>
                <Candidates
                  candidates={y.candidates}
                  legislature={x.name}
                  district={y.name}
                  handleChange={this.handleChange}
                />
              </ButtonGroup>
            </div>
          </div>
        ))}
      </TabPane>
    ));

    // Format Candidate metainfo for Voting Card
    const shareQuote = this.state.pressed.map(
      y => "\n" + y[0] + " – " + y[1] + ": " + y[2] + " (" + y[4] + ")"
    );

    return (
      <Row id="StateContainer">
        <Col className="noMarginCol" md="7">
          <div className="funbox">
            <p className="funboxTitle">
              <Link className="active" to="/">
                <IoBackArrow
                  size={18}
                  style={{ marginTop: 1 }}
                  color="#334858"
                  className="v-center"
                />
              </Link>
              &nbsp;<span className="v-center">{stateName}</span>
            </p>
            <div className="clearfix" style={{ paddingRight: "30rem" }}>
              <div />
            </div>
            <Nav pills tabs>
              {navTabs}
            </Nav>
            <div className="clearfix" style={{ paddingRight: "30rem" }}>
              <div />
            </div>
            <TabContent className="TabContent" activeTab={this.state.activeTab}>
              {tabPanes}
            </TabContent>
          </div>
        </Col>
        <Col className="noMarginCol votingColumn" md="5">
          <MyVotes
            ref={el => (this.componentRef = el)}
            pressed={this.state.pressed}
            legislatures={legislatures}
            stateName={stateName}
          />
          <p className="hintText text-muted" style={{ textAlign: "center" }}>
            Learn more about our grading system{" "}
            <Link
              style={{ textDecoration: "underline", color: "inherit" }}
              className="active"
              to="/About"
            >
              here.
            </Link>
          </p>
          <div id="ShareSection">
            {this.state.pressed.length === 0 ? (
              <br />
            ) : (
              <div lg="12" style={{ margin: 0, textAlign: "center" }}>
                <ReactToPrint
                  trigger={() => (
                    <TiPrinter className="printButton" size={22} color="#333" />
                  )}
                  content={() => this.componentRef}
                />

                <EmailShareButton
                  url="https://github.com"
                  subject={"My GunControlMap voting card"}
                  body={
                    "I filled out my Midterm Elections voting card at GunControlMap.com" +
                    shareQuote +
                    "\n"
                  }
                >
                  <TiMail size={22} color="#333" />
                </EmailShareButton>

                <FacebookShareButton
                  url="https://github.com"
                  quote={
                    "I filled out my Midterm Elections voting card at GunControlMap.com" +
                    shareQuote +
                    "\n"
                  }
                  hashtag="#GunControlMap"
                >
                  <FaFacebook size={18} color="#3B5998" />
                </FacebookShareButton>

                <TwitterShareButton
                  url="https://github.com"
                  title={
                    "I filled out my Midterm Elections voting card at GunControlMap.com" +
                    shareQuote +
                    "\n"
                  }
                  hashtags={[
                    "GunControlMap",
                    "GunControl",
                    "GunRegulation",
                    "Midterms"
                  ]}
                >
                  <FaTwitter size={18} color="#1DA1F2" />
                </TwitterShareButton>
              </div>
            )}
          </div>
        </Col>
      </Row>
    );
  }
}

// State > StateContainer > {tabPanes > Candidates}
class Candidates extends React.Component {
  constructor(props) {
    // props: candidates, legislature, district, handleChange
    super(props);
    this.state = {
      // each district has its own state to manage which radio button is pressed
      rSelected: ""
    };
    this.onRadioBtnClick = this.onRadioBtnClick.bind(this);
  }

  onRadioBtnClick(selected) {
    const index = this.state.rSelected.indexOf(selected);
    const stateIsEmpty = this.state.rSelected.length === 0;
    const currState = this.state.rSelected;
    if (index < 0 && stateIsEmpty) {
      this.setState({ rSelected: selected });
      this.props.handleChange("add", selected);
    } else if (index < 0 && !stateIsEmpty) {
      this.setState({ rSelected: selected });
      this.props.handleChange("change", selected, currState);
    } else {
      this.setState({ rSelected: "" });
      this.props.handleChange("delete", selected);
    }
  }

  render() {
    const joinedString =
      this.props.legislature + "," + this.props.district + ",";
    const candidates = this.props.candidates.map(x => (
      <Col xs="6" className="noMarginCol">
        <Button
          color="secondary"
          key={x.name + "." + x.img}
          onClick={() =>
            this.onRadioBtnClick(
              joinedString + x.name + "," + x.img + "," + x.grade
            )
          }
          active={
            this.state.rSelected ===
            joinedString + x.name + "," + x.img + "," + x.grade
          }
        >
          <img src={x.img} alt={x.name} />
          <p id="textbox">
            <span
              className="float-left"
              style={{ fontSize: ".9rem", overflow: "hidden", width: "90%" }}
            >
              {x.name}
            </span>
            <span
              className="letterGrades float-right"
              style={
                x.grade === "A" || x.grade === "B"
                  ? goodGradeStyle
                  : badGradeStyle
              }
            >
              {x.grade}
            </span>
          </p>
        </Button>
      </Col>
    ));
    return <Row className="candidates">{candidates}</Row>;
  }
}

// State > StateContainer > MyVotes
class MyVotes extends React.Component {
  render() {
    const votes = this.props.pressed.map(y => (
      <tr className="votes" key={y[3] + "votes"}>
        <td>
          <img src={y[3]} alt={y[2]} />
        </td>
        <td style={{ lineHeight: "1.2" }}>
          <span className="text-muted hintText">
            {y[0]} – {y[1]}
          </span>
          <br />
          {y[2]} ({y[4]})
        </td>
      </tr>
    ));

    return (
      <div style={{ maxWidth: 500, width: "100%", margin: "auto" }}>
        <div className="votingCardSpacer" />
        <div className="votingCard">
          <p style={{ width: "100%" }} className="smallCaps">
            MY {this.props.stateName.toUpperCase()} VOTING CARD
          </p>
          <div className="tableDiv">
            <Table id="votesList" flush>
            <tbody>
              {this.props.pressed.length === 0 ? (
                <div className="text-muted" style={{ fontSize: ".8rem" }}>
                  <em>Select a candidate to add them to your voting card.</em>
                </div>
              ) : (
                votes
              )}
              </tbody>
            </Table>
          </div>
          <div style={{ width: "100%" }}>
            <div
              style={{ position: "relative", bottom: 0, textAlign: "center" }}
              className="hintText text-muted"
            >
              created on guncontrolgrades.com
            </div>
          </div>
        </div>
      </div>
    );
  }
}






// APP
const App = () => {
  return (
    // delete when deploying to custom URL
    <Router basename="/d4d-map">
      <div>
        <NavHeader />
        <Route path="/State/:id" exact component={State} />
        <Route path="/About" exact component={About} />
        <Route path="/Contact" exact component={Contact} />
        <Route exact path="/" component={UsMap} />
      </div>
    </Router>
  );
};

export default App;



